body {
  background: #fefefe;
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: "Fira Sans", sans-serif;
}

h1, h2, h3, h4, h5, h6, label, span {
  font-weight: 500;
  font-family: "Fira Sans", sans-serif;
}

body, html, .App, #root, .auth-wrapper {
  width: 100%;
  height: 100%;
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  margin: auto;
}

.auth-inner {
  width: 450px;
  margin-left: 10px;
  background: #111111;
  box-shadow: 10px 5px 5px #222222;
  padding: 40px 55px 45px 55px;
  border-radius: 10px;
  transition: all 0.3s;
}

